<i18n>
{
  "en": {
    "PersonalInformation": "Personal information",
    "Avatar": "Avatar",
    "Nickname": "Nickname",
    "WeChat": "WeChat",
    "Unbound": "Unbound",
    "Alipay": "Alipay",
    "Logout": "Log out",
    "exitAccount": "Do you want to exit the current account?",
    "item": "Cancel | OK",
    "ReviseHeadImage": "Revise the head image",
    "MobilePhonePhoto": "Mobile phone photo",
    "LocalPhoto": "Local photo"
  },
  "zh": {
    "PersonalInformation": "个人信息",
    "Avatar": "头像",
    "Nickname": "昵称",
    "WeChat": "微信",
    "Unbound": "未绑定",
    "Alipay": "支付宝",
    "Logout": "退出登录",
    "exitAccount": "是否要推出当前账号?",
    "item": "取消 | 确定",
    "ReviseHeadImage": "修改头像",
    "MobilePhonePhoto": "手机拍照",
    "LocalPhoto": "本地照片"
  }
}
</i18n>

<template>
  <div class="personal">
    <head-top :title='$t("PersonalInformation")' :go-back="true"></head-top>
    <div class="container">
      <div class="avatar" @click="openAvatar">
        <div class="left">{{ $t("Avatar") }}</div>
        <div class="right">
          <span>
            <img style="height:1.3rem" src="../../assets/personal_images/avatar.png" alt="">
          </span>
        </div>
      </div>
      <div class="content">
        <router-link to="/rename" class="box bomPixel">
          <div class="left">{{ $t("Nickname") }}</div>
          <div class="right">
            <span>{{userInfo.name}}</span>
            <img src="../../assets/global_images/next.png" alt="">
          </div>
        </router-link>
        <router-link to="/personal" class="box bomPixel">
          <div class="left">{{ $t("WeChat") }}</div>
          <div class="right bind">
            <span>{{ $t("Unbound") }}</span>
          </div>
        </router-link>
        <router-link to="/personal" class="box">
          <div class="left">{{ $t("Alipay") }}</div>
          <div class="right nobind">
            <span>{{ $t("Unbound") }}</span><img src="../../assets/global_images/next.png" alt="">
          </div>
        </router-link>
      </div>
      <div class="logout" @click="logout">
        <strong>
          <img src="../../assets/personal_images/back.png" alt="">
          <span>{{ $t("Logout") }}</span>
        </strong>
      </div>
    </div>

    <!-- logout弹窗 -->
    <message-box :messageBox="messageBox"
                  icon="logout"
                  :title="$t('Logout')"
                  :describe="$t('exitAccount')"
                  :confirm="$tc('item',2)"
                  :cancel="$tc('item',1)"
                  @boxHandle="boxHandle"></message-box>

    <!-- 操作表 -->
    <action-sheet :actionSheet="avatarShow"
                  :index="1"
                  :title="$t('ReviseHeadImage')"
                  :list="[$t('MobilePhonePhoto'), $t('LocalPhoto')]"
                  :cancel="$tc('item',1)"
                  color="#575757"
                  @sheetHandle="sheetHandle"></action-sheet>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import messageBox from '../../components/MessageBox/index'
import actionSheet from '../../components/ActionSheet/index'
export default {
  name: 'personal',
  data () {
    return {
      avatarShow: false, // 操作表
      messageBox: false // 弹框显示隐藏
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  created () {
    if (!this.userInfo.name) {
      this.setUserInfo({name: 'Dennis'})
    }
  },
  mounted () {
    this.dataInteractionResponse(data => {
      if (data.msgType === '0x5122') {
        if (data.result && data.data.logout) {
          this.$router.push('/login')
        }
      }
    })
  },
  components: {
    headTop,
    messageBox,
    actionSheet
  },
  methods: {
    ...mapActions([
      'setUserInfo', 'sidebarSwitch'
    ]),
    // 弹框
    boxHandle (confirm) {
      if (confirm) {
        this.$router.push({'path': '/home'})
      }
      this.messageBox = false
    },
    // 操作表
    openAvatar () {
      this.avatarShow = true
    },
    sheetHandle (confirm, value, index) {
      this.avatarShow = false
    },
    logout () {
      this.sidebarSwitch(false)
      // 用户登出请求
      this.dataInteractionRequest({
        msgType: '0x5121'
      })
      if (this.ISDEBUG) {
        let str = '{"msgType":"0x5122","result":true,"data":{"logout":true}}'
        window.dataInteractionResponse(str)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.personal{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    background: #eeeeee;
    font-size: 26px;
    .avatar{
      margin-bottom: 22px;
      .wh-l(100%, 130px);
      background: #FFF;
      padding: 0 30px;
      .left{
        .wh-f(50%, 100%);
        text-align: left;
        font-size: 30px;
      }
      .right{
        .wh-f(50%, 100%);
        text-align: right;
        position: relative;
        span{
          .wh(90px, 90px);
          display: block;
          position: absolute;
          top: 50%;
          right: 0px;
          margin-top: -45px;
          padding-right: 0 !important;
          img{
            height: 90px;
          }
        }
      }
    }
    .content{
      .wh(100%, auto);
      .box{
        .wh-l(100%, 90px);
        display: block;
        background: #FFF;
        position: relative;
        padding: 0 30px;
        .left{
          .wh-f(50%, 100%);
          text-align: left;
          font-size: 30px;
        }
        .right{
          .wh-f(50%, 100%);
          text-align: right;
          padding-right: 40px;
          position: relative;
          color: #CCCCCC;
          span{
            .wh(100%, 100%);
            line-height: 90px;
            display: block;
          }
          img{
            height: 25px;
            position: absolute;
            top: 50%;
            right: 0px;
            margin-top: -12.5px;
          }
        }
      }
    }
    .logout{
      background: #aaaaaa;
      .wh-l(692px,100px);
      border-radius: 10px;
      margin:80px auto;
      color: #ffffff;
      font-size: 40px;
      position: relative;
      strong{
        .wh(auto, 100%);
        .f-center;
        img{
          height: 42px;
          position: absolute;
          top: 50%;
          left: 0;
          margin-top: -21px;
        }
        span{
          display: block;
          float: right;
          margin-left: 65px;
          font-size: 31px;
        }
      }
    }
  }
}
</style>



